import { useParams } from "react-router-dom";
import { Empty, Typography, PageHeader, Button, Descriptions, Skeleton, message } from 'antd';
import { useRequest } from 'ahooks';
import PostTags from '../../components/PostTags'
import { getPost, deletePost } from '../../api/post'
import './index.less'

const { Paragraph } = Typography;

const PostPage = () => {
  const { id } = useParams();
  const { data, error, loading } = useRequest(getPost, { defaultParams: [id] });
  console.log(data)
  document.title = data?.title || '404'

  const handleDelete = () => {
    if (id) {
      deletePost(id).then(() => message.success(''))
    }
  }
  return (
    data ? (
      <div className="post">
        <PageHeader
          className="site-page-header"
          title={data?.title}
          extra={[
            <Button key="3"></Button>,
            <Button key="2" onClick={handleDelete}></Button>,
          ]}
        >
          <Descriptions size="small" column={1}>
            <Descriptions.Item label="">{data.date}</Descriptions.Item>
            <Descriptions.Item label="">{data.size}</Descriptions.Item>
            <Descriptions.Item label="">{data.size}</Descriptions.Item>
            <Descriptions.Item label="">{data.url}</Descriptions.Item>
            <Descriptions.Item label="">{data.author}</Descriptions.Item>
            <Descriptions.Item label="">
              <PostTags
                initialTags={data.tags || []}
                postId={data._id}
                editable={true}
                closable={true}
              />
            </Descriptions.Item>
          </Descriptions>
        </PageHeader>
        <Skeleton loading={loading}>
          <Paragraph className="post-content">
            {data.content || '-'}
          </Paragraph>
        </Skeleton>
      </div>
    ) : <Empty />
  );
}

export default PostPage;